'use client'
import { useRef, useState, useEffect } from 'react';
import { listenPedidos } from '../../serverless/db/listenPedidos';
import ButtonImpres from './components/buttonImpres/'

export default function ListDocuments() {
    const [pedidos, setPedidos] = useState([]);

    // Array de referencias estable a las cards
    const cardRefs = useRef({});

    useEffect(() => {
        const unsubscribe = listenPedidos((newPedidos) => {
            setPedidos(newPedidos);
        });

        return () => unsubscribe();
    }, []);

    const formatCurrency = (value) => {
        return new Intl.NumberFormat('es-CL', { style: 'currency', currency: 'CLP' }).format(value);
    };

    const handleSendWhatsApp = (pedido, totalPedido) => {
        const phone = '56990059578';
        let message = `*Nuevo Pedido:*`;
        message += `*Cliente:* ${pedido.client}`;
        message += '*Detalle del pedido:* \n\n';

        pedido.products.forEach(item => {
            const totalItem = formatCurrency(item.quantity * item.product.selectedPrice.price);
            message += `${item.quantity}x ${item.product.nombre} ${item.product.peso}Kg - ${totalItem}\n`;
        });

        message += `\n*Total Pedido:* ${formatCurrency(totalPedido)}`;

        const whatsappUrl = `https://api.whatsapp.com/send?phone=${phone}&text=${encodeURIComponent(message)}`; 
        window.open(whatsappUrl, '_blank');
    };

    return (
        <div style={{ fontFamily: 'Arial, sans-serif', padding: '20px', backgroundColor: '#f0f2f5', width:"100vw" }}>
            <h1 style={{ color: '#333', borderBottom: '2px solid #ccc', paddingBottom: '10px', marginBottom: '20px' }}>
                Lista de Pedidos
            </h1>

            {pedidos.length === 0 ? (
                <p style={{ textAlign: 'center', color: '#666' }}>No hay pedidos para mostrar.</p>
            ) : (
                <div style={{ width:"100%", display: 'grid', gridTemplateColumns: 'repeat(auto-fill)', gap: '20px' }}>
                    {pedidos.map((pedido) => {

                        // Crear un ref si aÃºn no existe para este pedido
                        if (!cardRefs.current[pedido.id]) {
                            cardRefs.current[pedido.id] = useRef(null);
                        }

                        const totalPedido = (pedido.products || []).reduce((acc, item) =>
                            acc + (item.quantity * item.product.selectedPrice.price), 0
                        );

                        return (
                            <div
                                key={pedido.id}
                                ref={cardRefs.current[pedido.id]}  // Asignar el ref correspondiente
                                style={{
                                    width:"100%",
                                    background: '#fff',
                                    padding: '20px',
                                    borderRadius: '8px',
                                    boxShadow: '0 4px 8px rgba(0,0,0,0.1)'
                                }}
                            >
                                <h2 style={{ color: '#0056b3', marginBottom: '15px', borderBottom: '1px solid #eee', paddingBottom: '10px' }}>
                                    Pedido: {pedido.client}
                                </h2>

                                <table style={{ width: '100%', borderCollapse: 'collapse', marginBottom: '20px' }}>
                                    <thead>
                                        <tr style={{ backgroundColor: '#f2f2f2' }}>
                                            <th style={{ padding: '4px', border: '1px solid #ddd', textAlign: 'left' }}>Cant.</th>
                                            <th style={{ padding: '4px', border: '1px solid #ddd', textAlign: 'left' }}>Detalle</th>
                                            <th style={{ padding: '4px', border: '1px solid #ddd', textAlign: 'right' }}>Unitario</th>
                                            <th style={{ padding: '4px', border: '1px solid #ddd', textAlign: 'right' }}>Total</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {(pedido.products || []).map((item, index) => (
                                            <tr key={index}>
                                                <td style={{ padding: '4px', border: '1px solid #ddd' }}>{item.quantity}</td>
                                                <td style={{ padding: '4px', border: '1px solid #ddd' }}>{`${item.product.nombre} ${item.product.peso}Kg`}</td>
                                                <td style={{ padding: '4px', border: '1px solid #ddd', textAlign: 'right' }}>{formatCurrency(item.product.selectedPrice.price)}</td>
                                                <td style={{ padding: '4px', border: '1px solid #ddd', textAlign: 'right' }}>{formatCurrency(item.product.selectedPrice.price * item.quantity)}</td>
                                            </tr>
                                        ))}
                                    </tbody>
                                </table>

                                <div style={{ textAlign: 'right', fontWeight: 'bold', fontSize: '1.2em', color: '#333', marginTop: '15px' }}>
                                    Total Pedido: {formatCurrency(totalPedido)}
                                </div>

                                <div style={{display:"flex", gap:"10px", justifyContent:"flex-end", padding:"1rem"}}>
                                    <button
                                        onClick={() => handleSendWhatsApp(pedido, totalPedido)}
                                        style={{
                                            padding:'10px 20px',
                                            backgroundColor: '#25D366',
                                            color: 'white',
                                            border: 'none',
                                            borderRadius: '5px',
                                            cursor: 'pointer',
                                            fontWeight: 'bold',
                                            boxShadow: '0 2px 4px rgba(0,0,0,0.2)'
                                        }}
                                    >
                                        Enviar por WhatsApp
                                    </button>

                                    <ButtonImpres targetRef={cardRefs.current[pedido.id]} />
                                </div>
                            </div>
                        );
                    })}
                </div>
            )}
        </div>
    );
}
